.page-head {
    padding: 48px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 10;
    width: 100%;
    background-color: transparent;

    h1 {
        //font-size: 21px;
        text-align: center;
    }

    p {
        //font-family: 'Proxima Nova';
        text-align: center;
        padding: 0 24px;
    }

    &--bgimg {
        min-height: 269px;
        background-size: cover;
        background-position: center;
        display: flex;
        justify-content: center;
        position: relative;
        overflow: hidden;
        background-color: $blue;

        &:after {
            content: "";
            position: absolute;
            background-color: rgba($black, 0.5);
            z-index: 1;
            overflow: hidden;
            max-width: 100%;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        .page-head__title {
            color: $white;
            text-align: center;
            margin-bottom: $margin;
            z-index: 10;

            @include font-size(33px);
        }

        p {
            color: $white;
            z-index: 10;
        }
    }

    &--no-bg {
        .page-head__title {
            margin-bottom: 0;
        }
    }

    &__content {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: $post-width;
        padding: 0 20px;

        @include mq(800) {
            padding: 0;
        }


        .entry__terms {
            //margin-bottom: $margin;
            margin: 0 0 $margin 0;

            a {
                color: $white;
                text-transform: uppercase;
            }
        }

        .entry__published {
            color: $white;

            @include font-size(14px);
        }
    }

    &__headline {
        //background-color: red;
        width: 100%;
        padding: 0;
        margin-bottom: 0;
        text-transform: uppercase;
    }

    &__desc {
        //color: $white;
        //@include font-size(13px);
        max-width: $post-width;
        text-align: center;
        margin-top: 12px;
        color: $gray-dark;

        &--post {
            color: $white;
        }
    }

    &__icon {
        width: 50px;
        height: 50px;
        margin-bottom: 24px;
    }

    &.no-bg {
        .entry__terms {
            //margin-bottom: $margin;
            //margin: 0 0 $margin 0;

            a {
                color: $link-color !important;

                //text-transform: uppercase;
           
            }
        }

        .entry__published {
            color: $text-color;
        }
    }

    &.map {
        padding: 0 0 48px 0;
    }
}

.archive-header {
    padding: ($margin*2) 0 $margin 0;

    &__title {
        text-align: center;

        @include font-size(30px);

        text-transform: uppercase;
        margin: 0;
    }

    &__description {
        display: none;
    }
}


