////
/// Quote block.
///
/// @group  Blocks
/// @author Bryan Hoffman <bryan@spigotdesign.com>

////

.wp-block-quote {
	quotes: "“" "”";
    padding-left: $margin;
    position: relative;
    margin-bottom: $margin;

    &:before {
        content: open-quote;

        @include font-size(60px);

        position: absolute;
        left: -5px;
        top: -15px;
    }

    &:after {
        content: no-close-quote;
    }

    p {
        font-size: 20px;
        line-height: 1.5;
        font-family: $italic-font-family;
        color: $blue-dark;

        &:first-child {
            //display: inline;
       
        }
        &:last-child {
            margin-bottom: 0;
        }
    }

    cite {
        display: block;
        font-family: $med-font-family;
        font-style: normal;
        padding-left: $margin * 2;
        color: $blue-dark;

        &:before {
            content: "~ ";
        }
    }
}


