body {
	font-family: "acherusgrotesque-regular", sans-serif;
	color: #4A4A4A;
	line-height: 24px;
	font-style: normal;
	min-height: 100%;
	background-color: #F8F9F9;
	letter-spacing: .025em;
}

label {
	display: block;
	margin-bottom: 5px;
}

select,
textarea,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="tel"],
input[type="url"],
input[type="password"] {
	display: inline-block;
	height: auto;
	padding: 8px 12px;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.5;
	border-radius: 3px;
	font-family: "acherusgrotesque-regular", sans-serif;
}

textarea {
	height: auto;
}

textarea,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="tel"],
input[type="url"],
input[type="password"] {
	border: 1px solid #eaeded;
	transition: all .2s ease-in-out;
	color: #666B6F;
}

textarea:focus,
input[type="text"]:focus,
input[type="email"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus {
	border-color: rgba(168, 94, 206, 0.5);
	background: white;
	outline: 0;
	outline: thin dotted \9;
	/* IE6-9 */
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05), 0 0 10px rgba(168, 94, 206, 0.75);
	color: black;
}

input[type="radio"],
input[type="checkbox"] {
	margin: 3px 0;
	line-height: normal;
	cursor: pointer;
}

input[type="submit"],
input[type="reset"],
input[type="button"],
input[type="radio"],
input[type="checkbox"] {
	width: auto;
}

.uneditable-textarea {
	width: auto;
	height: auto;
}

select,
input[type="file"] {
	height: 28px;
	/* In IE7, the height of the select element cannot be changed by height, only font-size */
	line-height: 28px;
}

select {
	width: 220px;
	border: 1px solid #bbb;
}

select[multiple],
select[size] {
	height: auto;
}

fieldset {
	padding: 24px;
	margin-bottom: 24px;
	border-color: #eaeded;
}

legend {
	padding: 0 15px;
	text-transform: uppercase;
}

.radio,
.checkbox {
	min-height: 18px;
	padding-left: 18px;
}

.radio input[type="radio"],
.checkbox input[type="checkbox"] {
	float: left;
	margin-left: -18px;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "acherusgrotesque-medium", sans-serif;
	font-weight: normal;
	color: #004071;
	text-rendering: optimizelegibility;
	margin-bottom: 24px;
	line-height: 24px;
}

h1 {
	font-size: 47px;
	font-size: 2.9375rem;
	line-height: 1.0212766;
}

h2 {
	font-size: 37px;
	font-size: 2.3125rem;
	line-height: 1.2972973;
}

h2.entry__title {
	font-size: 20px;
	font-size: 1.25rem;
	line-height: 1.2;
}

h3 {
	font-size: 27px;
	font-size: 1.6875rem;
	line-height: 1.77777778;
	line-height: 1.25;
}

h4 {
	font-size: 23px;
	font-size: 1.4375rem;
	line-height: 1.04347826;
}

h5 {
	font-size: 20px;
	font-size: 1.25rem;
	line-height: 1.2;
}

h6 {
	font-size: 18px;
	font-size: 1.125rem;
	line-height: 1.33333333;
}

.entry__content ul, .entry__content ol {
	padding-left: 24px;
	margin-bottom: 24px;
}

p {
	margin-bottom: 24px;
}

.entry__content p:last-child {
	margin-bottom: 0;
}

p strong {
	font-family: "acherusgrotesque-bold", sans-serif;
}

blockquote {
	quotes: "“" "”";
}

blockquote p {
	text-indent: -0.42em;
}

blockquote p:before {
	content: open-quote;
}

blockquote p:after {
	content: no-close-quote;
}

blockquote p:last-of-type {
	margin-bottom: 0;
}

blockquote p:last-of-type:after {
	content: close-quote;
}

table {
	max-width: 100%;
	background-color: white;
	border-collapse: collapse;
	border-spacing: 0;
}

.app-content {
	margin: 0 auto;
	padding: 0;
	padding-top: 75px;
}

.fl-builder .app-content,
.woocommerce-page .app-content {
	max-width: 100%;
}

.transparent_header .app-content {
	padding-top: 0;
}

.has-sidebar .app-content {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
}

.has-sidebar .app-content .site-main {
	flex: 0 1 70%;
}

.has-sidebar .app-content .sidebar {
	flex: 0 1 27.5%;
}

.app-footer {
	border-top: 1px solid #eaeded;
	padding: 24px 0;
}

.app-footer__credit {
	font-size: 12px;
	font-size: 0.75rem;
	line-height: 2;
	text-align: center;
}

.app-footer__branding {
	display: flex;
	justify-content: center;
}

.app-footer__branding-link svg {
	width: 152px;
	height: 23px;
}

.menu--footer {
	margin: 24px 0;
}

.menu--footer .menu__items {
	list-style: none;
	display: flex;
	justify-content: center;
}

.menu--footer .menu__link {
	color: #666B6F;
	padding: 12px;
}

.menu--footer .menu__link:hover {
	color: #107bbd;
}

.app-header {
	background-color: white;
	height: 75px;
	width: 100%;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 1000;
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
}

.app-header.invisible {
	transform: translate(0, -116px);
	transition: transform 0.2s;
	opacity: 0;
}

.app-header.detached {
	position: fixed;
	transition: transform 0.3s, height 0.3s, background 0.4s, opacity 0.3s;
	top: 0;
	height: 50px;
}

.admin-bar .app-header.detached {
	top: 32px;
}

@media (max-width: 782px) {
	.admin-bar .app-header.detached {
		top: 0;
	}
}

.admin-bar .app-header {
	top: 32px;
}

@media (max-width: 782px) {
	.admin-bar .app-header {
		top: 46px;
	}
}

.admin-bar.fl-builder-edit .app-header {
	top: 0;
}

.app-header__wrap {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	padding: 0 24px;
}

.app-header__branding {
	transition: all .3s ease-in-out;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.detached .app-header__branding {
	margin-top: 0;
}

.app-header__branding-link {
	width: 150px;
	height: 35px;
}

@media (min-width: 600px) {
	.app-header__branding-link {
		width: 200px;
		height: 47px;
	}
}

.detached .app-header__branding-link {
	width: 150px;
	height: 35px;
}

.app-header__branding-link svg {
	width: 150px;
	height: 35px;
	transition: all .3s ease-in-out;
}

@media (min-width: 600px) {
	.app-header__branding-link svg {
		width: 200px;
		height: 47px;
	}
}

.detached .app-header__branding-link svg {
	width: 150px;
	height: 35px;
	padding-top: 3px;
}

.transparent_header .app-header {
	background-color: transparent;
	box-shadow: none;
}

.transparent_header .app-header.detached {
	background-color: white;
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
}

.app-search {
	margin: 0;
	position: absolute;
	right: 0;
}

.app-search ::-webkit-input-placeholder {
	color: white;
	font-family: "acherusgrotesque-bold", sans-serif;
	font-weight: normal;
}

.app-search :-moz-placeholder {
	color: white;
	font-family: "acherusgrotesque-bold", sans-serif;
	font-weight: normal;
}

.app-search ::-moz-placeholder {
	color: white;
	font-family: "acherusgrotesque-bold", sans-serif;
	font-weight: normal;
}

.app-search :-ms-input-placeholder {
	color: white;
	font-family: "acherusgrotesque-bold", sans-serif;
	font-weight: normal;
}

.app-search input[type="submit"] {
	display: none;
}

.app-search input[type="search"] {
	border-radius: 200px;
	width: 32px;
	height: 100%;
	text-indent: 20px;
	cursor: pointer;
	border: none;
	padding: 5px;
	padding-left: 15px;
	font-family: "acherusgrotesque-bold", sans-serif;
	font-weight: normal;
	background: transparent url(../svg/search.svg) 10px 50% no-repeat;
	background-size: 16px 16px;
	color: #004071;
	outline: none;
	height: 33px;
	transition: all .3s ease-in-out;
	font-size: 14px;
	font-size: 0.875rem;
	line-height: 1.71428571;
}

.app-search input[type="search"]:focus {
	width: 320px;
	outline: none;
	color: white;
	background-image: url(../svg/search-white.svg);
	background-color: #2DA3ED;
	box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.2);
	cursor: text;
}

@media (max-width: 599px) {
	.app-search input[type="search"]:focus {
		width: 280px;
	}
}

