	margin-bottom: 0;
}

.comments-template {
	max-width: 900px;
	margin: 0 auto;
	background-color: white;
	padding: 24px;
	box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.2);
	border-top: none;
	border-radius: 3px;
	margin-bottom: 24px;
