/**
   * Product loops
   */
  .products ul,
  ul.products {
    margin: 0 0 1em;
    padding: 0;
    list-style: none outside;
    clear: both;

    @include clearfix;

    li {
      list-style: none outside;
    }
  }

  ul.products li.product {
    .onsale {
      top: 0;
      right: 0;
      left: auto;
      margin: -0.5em -0.5em 0 0;
    }

    h3,
    .woocommerce-loop-product__title,
    .woocommerce-loop-category__title {
      padding: 0.5em 0;
      margin: 0;
      font-size: 1em;
    }

    a {
      text-decoration: none;
    }

    a img {
      width: 100%;
      height: auto;
      display: block;
      margin: 0 0 1em;
      box-shadow: none;
    }

    strong {
      display: block;
    }

    .star-rating {
      font-size: 0.857em;
    }

    .button {
      margin-top: 1em;
    }

    .price {
      color: $highlight;
      display: block;
      font-weight: normal;
      margin-bottom: 0.5em;
      font-size: 0.857em;

      del {
        color: inherit;
        opacity: 0.5;
        display: inline-block;
      }

      ins {
        background: none;
        font-weight: 700;
        display: inline-block;
      }

      .from {
        font-size: 0.67em;
        margin: -2px 0 0 0;
        text-transform: uppercase;
        color: rgba(desaturate($highlight, 75%), 0.5);
      }
    }
  }

  .woocommerce-result-count {
    margin: 0 0 1em;
  }

  .woocommerce-ordering {
    margin: 0 0 1em;

    select {
      vertical-align: top;
    }
  }


